import { Component} from '@angular/core';
import { IonicPage, NavController} from 'ionic-angular';
import { FormBuilder, FormGroup, Validators} from '@angular/forms';
import { MainPage } from '../main/main';
import { HttpClient } from '@angular/common/http';


/**
 * Generated class for the SignupPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html',
})
export class SignupPage {
  //signUpService: SignUpProvider;
  signUpForm: FormGroup;
  submitAttempt: boolean = false;
         url;
  constructor(public navCtrl: NavController, public formBuilder: FormBuilder, public http : HttpClient) {
      console.log('Hello SignUpProvider Provider');
      this.url ="https://visitdelawarebackendv2.herokuapp.com";
  
      this.signUpForm = formBuilder.group({
      fullName: ['',Validators.compose([Validators.maxLength(30), Validators.pattern('[a-zA-Z ]*'), Validators.required])],
      email: ['',Validators.compose([Validators.required, Validators.email.bind(this)])],
      birthDate:[''],
      zipCode:['',Validators.compose([Validators.minLength(5),Validators.maxLength(5), Validators.required])],
      wheelChair: []
  });
  }

  save(){
    if(!this.signUpForm.valid){
      this.submitAttempt = true;
      return;
    }
    this.addProfile(this.signUpForm.value);
    this.gotoMainPage();
    console.log("success!")
    console.log(this.signUpForm.value);
    console.log(this.signUpForm.value.wheelChair ? 1 : 0);

    if(this.signUpForm.value.wheelChair) {
      console.log("we need to do something");
    }
  }
  addProfile(theObject :any){
 
    this.http.post(this.url +'/' +'profiles', JSON.stringify(theObject))
    .subscribe(data => {
      console.log(data);
  });
  }
    gotoMainPage(){
      //push another page onto the history stack
          //causing the nav controller to animate the new page in
          this.navCtrl.push(MainPage);
      }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SignupPage');
  }
  
  }




